import './App.css';
import NavBar from './NavBar';
import ChartsCube from './Chartscube';

import { useState, useEffect} from "react";
import { onValue, ref } from "firebase/database";
import { initializeApp } from "firebase/app";

import { getDatabase } from "firebase/database";

import React from 'react';

const firebaseConfig = {

  apiKey: "AIzaSyCmP0AXVITD4tNhKu78tYRFSBYSv1Ov3Ac",

  authDomain: "water-45a25.firebaseapp.com",

  databaseURL: "https://water-45a25-default-rtdb.firebaseio.com",

  projectId: "water-45a25",

  storageBucket: "water-45a25.appspot.com",

  messagingSenderId: "43987865741",

  appId: "1:43987865741:web:225d047878793a4fe592c4",

  measurementId: "G-9R19GGG365"

};


// Initialize Firebase


function App() {
  const [data, setData] = useState([]);
  const [locs, setLocs] = useState([]);
  const [refe, setRefresh] = useState(0);
  const [loc,setLoc]=useState('Choose a location');
  const [labels, setLabels] = useState([]); // Normalized timestamps
  const [ph, setPh] = useState([]);
  const [tds, setTds] = useState([]);
  const [temp, setTemp] = useState([]);
  const [turbidity, setTurbidity] = useState([]);
  const [seeChart, setSeeChart] = useState(false);
  window._debug = {data, labels, ph, tds, temp, turbidity};
  

  const handleLocChange = (e) => {
    if (e.target.value === 'Choose a location') {
      setSeeChart(false);
      return;
    } 
    // alert("hi")
    setSeeChart(true);
    setLoc(e.target.value);
    setLabels([]);
    setPh([]);
    setTds([]);
    setTemp([]);
    setTurbidity([]);
    console.log(data);
    console.log(e.target.value);
    console.log(data[e.target.value]);
    var _ph = [];
    var _tds = [];
    var _temp = [];
    var _turbidity = [];
    var _labels = [];
    console.log(e)

    for (const [key, reading] of Object.entries(data[e.target.value])){
      console.log(reading); 
      console.log(key); 
      _labels.push(new Date(parseInt(reading.timestamp) * 1000).toLocaleDateString() );
      _ph.push(parseFloat(reading.ph));
      _tds.push(parseFloat(reading.tds));
      _temp.push(parseFloat(reading.temperature));
      _turbidity.push(parseFloat(reading.turbid));
    };
    setLabels(_labels);
    setPh(_ph);
    setTds(_tds);
    setTemp(_temp);
    setTurbidity(_turbidity);
  }
  useEffect(() => {
    const app = initializeApp(firebaseConfig);

    const db = getDatabase(app);

    const query = ref(db, "/");
    return onValue(query, (snapshot) => {

      const data = snapshot.val();
      console.log(data);
      setData(data);
      setLocs([])
      for(let i in data){
        setLocs((locs)=>[...locs,i])
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps


    });
  
  }, [refe]);
  const refresh = () => {
    setRefresh(refe + 1);
    handleLocChange({target:{value:loc}})

  
  }
    // Reading contains:
  /* {"<timestamp>": {
    "location": <loc>.toUpperCase(),
    "ph": <value from 0 to 14>,
    "tds": <value from 0 to 1000>,
    "temp": <value from 0 to 100>
    "timestamp": <timestamp>
    "turbidity": <value from 0 to 10>
  }}
  */


  return (
    <div className="App">
      <NavBar locations={locs} handleLocChange={handleLocChange} />
        <div className="px-4 py-2 content-end locref">
            <button onClick={refresh} className="inline-block text-sm px-4 py-2 border rounded text-white hover:border-transparent  bg-blue-500 hover:text-blue-500 hover:bg-white ">REFRESH</button>
   
   
      <h1 className=" text-4xl font-extrabold   text-gray-900 md:text-5xl  center text-center">{loc}</h1>
      </div>
      
      {seeChart && <ChartsCube labels={labels} ph={ph} tds={tds} temp={temp} turbidity={turbidity} />}
    
      </div>
  );
}

export default App;
